.OurClients {
  height: 223px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 100px;
  padding: 0px 0px;
  width: 1250px;
  margin: auto;
}
.OurClientsWrapper{
  background-image: -webkit-linear-gradient(306deg, #ff4b1f 0%, #ff9068 100%);
  background-image: -o-linear-gradient(306deg, #ff4b1f 0%, #ff9068 100%);
  background-image: linear-gradient(144deg, #ff4b1f 0%, #ff9068 100%);
}

.OurClients div:nth-child(1) {
  -ms-flex: 1 1;
      flex: 1 1;
  cursor: pointer;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}
.OurClients div:nth-child(1):hover {
  -ms-flex: 1 1;
      flex: 1 1;
  cursor: pointer;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}
.OurClients div:nth-child(2) {
  -ms-flex: 3 1;
      flex: 3 1;
}
.OurClients div:nth-last-child(1) {
  -ms-flex: 1 1;
      flex: 1 1;
  cursor: pointer;
}
.OurClients div:nth-last-child(1):hover {
  -ms-flex: 1 1;
      flex: 1 1;
  cursor: pointer;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.h2_OurClients_header{
  color:#fff;
}
.h2_OurClients_description{
  font-size: 18px;
  color: #fff;
}
.div_OurClients_clientListWrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.div_OurClients_clientListWrapper p{
  color:#fff;
  font-size: 18px;
  width: 350px;
  text-transform: uppercase;
}


