@media screen and (min-width: 578px) and (max-width:768px) {
    .Footer{
        /* padding: 0px 24px; */
        -ms-flex-pack: justify;
            justify-content: space-between;
        width:560px;
        margin: auto;
        height: 83px;
    }
    .Footer span{
        -ms-flex:2 1;
            flex:2 1;
        text-align: left;
        font-size: 14px;	line-height: 28px;
    }
    .Footer ul{
        display: none;
        -ms-flex:2 1;
            flex:2 1;
        -ms-flex-pack: justify;
            justify-content: space-between;
        font-size: 16px;	font-weight: 600;	line-height: 19px;
    }
    .img_totop{
        display:block;
        padding: 10px;
    }
}
@media screen and (min-width: 320px) and (max-width:577px) {
    .Footer{
        /* padding: 0px 24px; */
        -ms-flex-pack: justify;
            justify-content: space-between;
        width:310px;
        margin: auto;
        height: 83px;
    }
    .Footer span{
        -ms-flex:2 1;
            flex:2 1;
        text-align: left;
        font-size: 10px;	line-height: 28px;
    }
    .Footer ul{
        display: none;
        -ms-flex:2 1;
            flex:2 1;
        -ms-flex-pack: justify;
            justify-content: space-between;
        font-size: 16px;	font-weight: 600;	line-height: 19px;
    }
    .img_totop{
        display:block;
        padding: 10px;
    }
}


