.Ticker {
  background-image: -webkit-linear-gradient(306deg, #ff4b1f 0%, #ff9068 100%);
  background-image: -o-linear-gradient(306deg, #ff4b1f 0%, #ff9068 100%);
  background-image: linear-gradient(144deg, #ff4b1f 0%, #ff9068 100%);
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  padding: 0px 100px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 16px;
  letter-spacing: 1.71px;
  text-align: center;
  line-height: 22px;
}

.Ticker h3 {
  color: #ffffff !important;
  height: 22px;
  margin: 0px 20px;
}



