@media screen and (min-width: 1024px) and (max-width:1439px) {
    .Contact{
        height: 558px;
        background-position: center;
    }
    .div_Contact_contactData h2{
        font-size: 36px;	letter-spacing: -0.49px;	line-height: 48px; text-transform: lowercase; font-style: italic;
    }
    .div_Contact_contactData h1{
        font-size: 40px;	font-weight: bold;	letter-spacing: -0.55px;	line-height: 48px;
    }
    .div_Contact_contactData p{
        font-size: 16px;	font-weight: 600;	letter-spacing: 1.71px;	line-height: 22px;
    }
    .Contact div{
        width: 936px;
        margin: auto;
    }
    .div_Contact_contactData h1{
        margin-top:0px;
        margin-bottom: 50px; 
    }

    
 }
 


